.parking-item {
	display: flex;
	flex-direction: column;
	width: 23%;
	height: fit-content;
	margin: 10px 0;
	overflow: hidden;
	border-radius: 10px;
	background-color: #fff;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
	font-family: "Open Sans", sans-serif;
	text-align: center;
	transition: max-height 0.3s ease-in-out;

	img {
		width: 100%;
		height: 200px;
		object-fit: cover;
	}

	.title {
		margin: 0;
		padding: 10px;
		overflow: hidden;
		border-radius: 5px;
		background-color: #1e88e5;
		color: #fff;
		font-size: 1.25rem;
		font-weight: 600;
		letter-spacing: 0.0125em;
		line-height: 1.75rem;
		text-overflow: ellipsis;
		text-shadow: 2px 2px 2px rgba(0, 0, 0, 1);
		white-space: nowrap;
	}

	p {
		margin: 0 10px;
		padding: 8px 0;

		color: #666;

		font-size: 1rem;
		font-weight: 400;
		letter-spacing: 0.03125em;
		line-height: 1.5rem;
	}

	button {
		width: 100%;
		margin-top: auto;
		padding: 12px;
		border: none;
		border-radius: 5px;
		outline: 0;
		background-color: transparent;
		color: #111;
		font-size: 18px;
		text-align: center;
		cursor: pointer;
	}

	&:hover {
		transform: translateY(-5px);
		box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
	}
}

@media (max-width: 600px) {
	.parking-item {
		width: 100%;
	}
}

@media (min-width: 601px) and (max-width: 992px) {
	.parking-item {
		width: 45%;
	}
}

@media (min-width: 1800px) {
	.parking-item {
		width: 18%;
	}
}

.fa-chevron-down {
	transform: rotate(0deg);
	transition: transform 0.5s ease-in-out;
}

.chevron-open {
	transform: rotate(180deg);
}

.extra-content {
	overflow: hidden;
	transition: height 0.5s ease-in-out;
}

.key {
	font-weight: 600;
}
